const {ccclass, property} = cc._decorator;

@ccclass
export default class ShowHOrHideObj extends cc.Component {

    @property(cc.Node)
    objs:cc.Node [] = []

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }


    public ShowObjByIndex(index:number)
    {
        this.objs[index].active = true;
    }

    public HideObjByIndex(index:number)
    {
        this.objs[index].active = false;
    }

    public HideAllObjs()
    {
        for(var i = 0; i < this.objs.length; i++)
        {
            this.objs[i].active = false;
        }
    }


    public ShowAllObjs()
    {
        for(var i = 0; i < this.objs.length; i++)
        {
            this.objs[i].active = true;
        }
    }

    public ShowOnlyObjByIndex(index:number)
    {
        this.HideAllObjs();
        this.ShowObjByIndex(index);
    }
    // update (dt) {}
}
